.container {
  width: auto;
  height: 10%;
  margin-bottom: 1rem;
  margin-top: 1rem;
  border-radius: 10rem;
  background-color: rgba(192, 192, 192, 0.2);

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  padding-left: 1rem;
  padding-right: 1rem;
}

.form {
  width: 400px;
  height: 100%;
  
  display: flex;
  align-items: center;
  justify-content: center;
}

.input {
  width: 90%;
  height: 70%;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  margin-left: 1.2rem;
  border: 0.01rem solid rgba(192, 192, 192, 0.5);
  border-right: none;
  background-color: rgba(192, 192, 192, 0.1);
  
  padding-left: 1.2rem;
  padding-right: 0.5rem;

  font-size: medium;
  color: aliceblue;
}

.button {
  border-radius: 50%;
  background-color: rgba(192, 192, 192, 0.1);
  
  width: 5rem;
  height: 90%;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5%;
}

.button:hover {
  transform: scale(1.2);
  transition: all .2s;
  color: var(--color-accent);
  filter: brightness(125%);
}

.hbutton {
  border-radius: 50%;
  background-color: rgba(192, 192, 192, 0);
  
  width: 5rem;
  height: 90%;
  
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5%;
  
  border: 0.43rem solid rgb(80, 255, 82);
}

.hbutton:hover {
  transform: scale(1.2);
  transition: all .2s;
  color: var(--color-accent);
  filter: brightness(125%);
}

.search {
  width: 80%;
  height: 80%;
  border-radius: 50%;
  border-style: none;
  margin: 0;
  padding: 0;
  background-color: rgba(192, 192, 192, 0);
}

.search:hover {
  cursor: pointer;
}

.magnify {
  font-size: 300%;
}

.powercontainer {
  border-radius: 50%;
  background-color: rgba(192, 192, 192, 0.3);
  border-style: none;
  margin-left: 1rem;
  
  width: 5rem;
  height: 80%;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5%;
}

.powercontainer:hover {
  transform: scale(1.2);
  color: var(--color-accent);
  filter: brightness(125%);
}

.power {
  width: 100%;
  height: 100%;
}
